"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Separator } from "@/components/ui/separator"
import JobCard from "@/components/JobCard"
import type { Job } from "@/app/api/jobs/route"

interface UserProfile {
  firstName: string
  lastName: string
  email: string
  location: string
  joinDate: string
}

interface Application {
  id: string
  job: Job
  appliedDate: string
  status: 'pending' | 'reviewed' | 'interview' | 'rejected' | 'accepted'
}

export default function DashboardPage() {
  const [user] = useState<UserProfile>({
    firstName: "Alex",
    lastName: "Mthembu",
    email: "alex.mthembu@email.com",
    location: "Cape Town, Western Cape",
    joinDate: "2024-01-10"
  })

  const [savedJobs, setSavedJobs] = useState<Job[]>([])
  const [applications, setApplications] = useState<Application[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    // Simulate loading user data
    const loadDashboardData = async () => {
      setLoading(true)
      
      // Simulate API calls
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      // Mock saved jobs
      const mockSavedJobs: Job[] = [
        {
          id: '1',
          title: 'Part-time Sales Assistant',
          company: 'Pick n Pay',
          location: 'Cape Town, Western Cape',
          description: 'Join our friendly team as a part-time sales assistant...',
          requirements: ['Grade 10 or higher', 'Good communication skills'],
          salary: 'R80-100 per hour',
          type: 'Part-time',
          category: 'Retail',
          postedDate: '2024-01-15'
        },
        {
          id: '3',
          title: 'Math Tutor',
          company: 'StudyBuddy Tutoring',
          location: 'Johannesburg, Gauteng',
          description: 'Help Grade 8-10 students with mathematics...',
          requirements: ['Grade 12 with strong math results', 'Patient and encouraging'],
          salary: 'R120-150 per hour',
          type: 'Part-time',
          category: 'Tutoring',
          postedDate: '2024-01-13'
        }
      ]

      // Mock applications
      const mockApplications: Application[] = [
        {
          id: 'app1',
          job: {
            id: '2',
            title: 'Weekend Waiter/Waitress',
            company: 'Ocean Basket',
            location: 'Durban, KwaZulu-Natal',
            description: 'Looking for energetic teenagers to join our weekend team...',
            requirements: ['Grade 11 or 12', 'Available weekends'],
            salary: 'R70 per hour + tips',
            type: 'Part-time',
            category: 'Food Service',
            postedDate: '2024-01-14'
          },
          appliedDate: '2024-01-16',
          status: 'pending'
        },
        {
          id: 'app2',
          job: {
            id: '5',
            title: 'Shop Assistant',
            company: 'Woolworths',
            location: 'Port Elizabeth, Eastern Cape',
            description: 'Join our team as a shop assistant...',
            requirements: ['Grade 10 minimum', 'Friendly demeanor'],
            salary: 'R75-90 per hour',
            type: 'Part-time',
            category: 'Retail',
            postedDate: '2024-01-11'
          },
          appliedDate: '2024-01-12',
          status: 'interview'
        }
      ]

      setSavedJobs(mockSavedJobs)
      setApplications(mockApplications)
      setLoading(false)
    }

    loadDashboardData()
  }, [])

  const getStatusColor = (status: Application['status']) => {
    switch (status) {
      case 'pending': return 'bg-yellow-100 text-yellow-800'
      case 'reviewed': return 'bg-blue-100 text-blue-800'
      case 'interview': return 'bg-purple-100 text-purple-800'
      case 'accepted': return 'bg-green-100 text-green-800'
      case 'rejected': return 'bg-red-100 text-red-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getStatusText = (status: Application['status']) => {
    switch (status) {
      case 'pending': return 'Under Review'
      case 'reviewed': return 'Reviewed'
      case 'interview': return 'Interview Scheduled'
      case 'accepted': return 'Accepted'
      case 'rejected': return 'Not Selected'
      default: return 'Unknown'
    }
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    return date.toLocaleDateString('en-ZA', { 
      year: 'numeric', 
      month: 'short', 
      day: 'numeric' 
    })
  }

  if (loading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-4"></div>
          <div className="h-4 bg-gray-200 rounded w-1/2 mb-8"></div>
          <div className="grid md:grid-cols-4 gap-6">
            <div className="h-32 bg-gray-200 rounded"></div>
            <div className="h-32 bg-gray-200 rounded"></div>
            <div className="h-32 bg-gray-200 rounded"></div>
            <div className="h-32 bg-gray-200 rounded"></div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-4xl font-bold mb-2">Welcome back, {user.firstName}!</h1>
        <p className="text-muted-foreground">
          Track your job applications and discover new opportunities
        </p>
      </div>

      {/* Stats Cards */}
      <div className="grid md:grid-cols-4 gap-6 mb-8">
        <Card>
          <CardHeader className="pb-2">
            <CardDescription>Applications</CardDescription>
            <CardTitle className="text-3xl">{applications.length}</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-xs text-muted-foreground">
              Total applications submitted
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardDescription>Saved Jobs</CardDescription>
            <CardTitle className="text-3xl">{savedJobs.length}</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-xs text-muted-foreground">
              Jobs saved for later
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardDescription>Interviews</CardDescription>
            <CardTitle className="text-3xl">
              {applications.filter(app => app.status === 'interview').length}
            </CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-xs text-muted-foreground">
              Interview opportunities
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardDescription>Profile Views</CardDescription>
            <CardTitle className="text-3xl">24</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-xs text-muted-foreground">
              Employers viewed your profile
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Main Content */}
      <div className="grid lg:grid-cols-3 gap-8">
        {/* Left Column - Applications & Saved Jobs */}
        <div className="lg:col-span-2">
          <Tabs defaultValue="applications" className="w-full">
            <TabsList className="grid w-full grid-cols-2">
              <TabsTrigger value="applications">My Applications</TabsTrigger>
              <TabsTrigger value="saved">Saved Jobs</TabsTrigger>
            </TabsList>
            
            <TabsContent value="applications" className="space-y-4">
              <div className="flex justify-between items-center">
                <h3 className="text-lg font-semibold">Recent Applications</h3>
                <Button variant="outline" size="sm" asChild>
                  <a href="/jobs">Find More Jobs</a>
                </Button>
              </div>
              
              {applications.length === 0 ? (
                <Card>
                  <CardContent className="text-center py-8">
                    <p className="text-muted-foreground mb-4">
                      You haven't applied to any jobs yet
                    </p>
                    <Button asChild>
                      <a href="/jobs">Browse Jobs</a>
                    </Button>
                  </CardContent>
                </Card>
              ) : (
                <div className="space-y-4">
                  {applications.map((application) => (
                    <Card key={application.id}>
                      <CardHeader>
                        <div className="flex justify-between items-start">
                          <div>
                            <CardTitle className="text-lg">{application.job.title}</CardTitle>
                            <CardDescription>{application.job.company}</CardDescription>
                            <p className="text-sm text-muted-foreground mt-1">
                              {application.job.location}
                            </p>
                          </div>
                          <Badge className={getStatusColor(application.status)}>
                            {getStatusText(application.status)}
                          </Badge>
                        </div>
                      </CardHeader>
                      <CardContent>
                        <div className="flex justify-between items-center">
                          <div>
                            <p className="text-sm text-muted-foreground">
                              Applied on {formatDate(application.appliedDate)}
                            </p>
                            <p className="font-medium text-primary">{application.job.salary}</p>
                          </div>
                          <Button variant="outline" size="sm" asChild>
                            <a href={`/jobs/${application.job.id}`}>View Job</a>
                          </Button>
                        </div>
                      </CardContent>
                    </Card>
                  ))}
                </div>
              )}
            </TabsContent>
            
            <TabsContent value="saved" className="space-y-4">
              <div className="flex justify-between items-center">
                <h3 className="text-lg font-semibold">Saved Jobs</h3>
                <Button variant="outline" size="sm" asChild>
                  <a href="/jobs">Find More Jobs</a>
                </Button>
              </div>
              
              {savedJobs.length === 0 ? (
                <Card>
                  <CardContent className="text-center py-8">
                    <p className="text-muted-foreground mb-4">
                      You haven't saved any jobs yet
                    </p>
                    <Button asChild>
                      <a href="/jobs">Browse Jobs</a>
                    </Button>
                  </CardContent>
                </Card>
              ) : (
                <div className="space-y-4">
                  {savedJobs.map((job) => (
                    <JobCard key={job.id} job={job} />
                  ))}
                </div>
              )}
            </TabsContent>
          </Tabs>
        </div>

        {/* Right Column - Profile & Quick Actions */}
        <div className="space-y-6">
          {/* Profile Card */}
          <Card>
            <CardHeader>
              <CardTitle>Your Profile</CardTitle>
              <CardDescription>Keep your information up to date</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <p className="font-medium">{user.firstName} {user.lastName}</p>
                <p className="text-sm text-muted-foreground">{user.email}</p>
                <p className="text-sm text-muted-foreground">{user.location}</p>
              </div>
              
              <Separator />
              
              <div>
                <p className="text-sm text-muted-foreground">
                  Member since {formatDate(user.joinDate)}
                </p>
              </div>
              
              <Button variant="outline" className="w-full">
                Edit Profile
              </Button>
            </CardContent>
          </Card>

          {/* Quick Actions */}
          <Card>
            <CardHeader>
              <CardTitle>Quick Actions</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <Button className="w-full" asChild>
                <a href="/jobs">Browse New Jobs</a>
              </Button>
              <Button variant="outline" className="w-full">
                Update Resume
              </Button>
              <Button variant="outline" className="w-full">
                Job Alerts Settings
              </Button>
            </CardContent>
          </Card>

          {/* Tips Card */}
          <Card>
            <CardHeader>
              <CardTitle>Job Search Tips</CardTitle>
            </CardHeader>
            <CardContent>
              <ul className="text-sm space-y-2 text-muted-foreground">
                <li>• Complete your profile to attract employers</li>
                <li>• Apply to jobs within 24 hours of posting</li>
                <li>• Follow up on applications after a week</li>
                <li>• Customize your application for each job</li>
              </ul>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
